.con{
    display: flex;
    flex-flow: column wrap;
    align-items: center;
}

.dashcontainer {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;  
    max-width: 500px;
    border: 1px solid #ebedf0;
    align-content: space-between;
    align-items: flex-start;
}
.complete {
  background: greenyellow;
  border: 1px solid rgba(27,31,35,0.06);
  border-radius: 2.5px;
  padding: 5px;
  width: 21px;
  height: 21px;
  margin: 7.5px;
  line-height: 50px;
  color: white;
  font-weight: bold;
  font-size: 3em;
  text-align: center;
}

.incomplete {
  background: #ebedf0;
  border: 1px solid rgba(27,31,35,0.06);
  border-radius: 2.5px;
  padding: 5px;
  width: 21px;
  height: 21px;
  margin: 7.5px;
  line-height: 50px;
  color: white;
  font-weight: bold;
  font-size: 3em;
  text-align: center;
}